body {
    width: 100%;
    height: auto;
    margin: 0 auto;
  }
  
  body {
    font-family: Kulim Park;
  }

  


  #collection-products {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 50px 0px;
    margin-top: 25em !important;
  }

  .product-container {
    display: flex;
    justify-content: center;
    align-items: center;
    display: grid;
    margin: auto;
    flex-wrap: wrap;
    grid-template-columns: repeat(2, 1fr);
  }
  @media only screen and (max-width: 568px) {
    .product-container {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  @media only screen and (min-width: 768px) {
    .product-container {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  @media only screen and (min-width: 900px) {
    .product-container {
      grid-template-columns: repeat(3, 1fr);
    }
  }
  
  .product-box {
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    height: auto;
    margin: 0.7em;
    gap: 0.7em !important;
    flex-direction: column;
  }
  
  .product-img {
    margin-bottom: 10px;
    padding: 0.2em;
    border: 1px solid #cacaca;
    width: 140px;
    height: 170px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .product-img img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: center;
       object-position: center;
  }
  .product-img img:hover {
    transform: scale(1.03);
    transition: all ease 0.2s;
    cursor: pointer;
  }
  @media only screen and (max-width: 768px) {
    .product-img .product-img {
      width: 100%;
      margin-bottom: 1em;
    }
  }
  
  .product-text-container {
    margin-top: -10px;
    font-size: 0.7rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 140px;
  }
  
  .product-text {
    height: 40px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
  }
  .product-text strong {
    color: #2c2c2c;
  }
  .product-text p {
    align-self: center;
    margin-top: 0.3em;
    color: #2e2e2e;
  }
  
  .cart-like {
    display: flex;
  }
  .cart-like a {
    color: #131313;
    width: 20px;
    height: 20px;
    padding: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    border: 1px solid #8a8a8a;
    border-radius: 50%;
    margin: 0px 5px;
    transition: all ease 0.2s;
  }
  .cart-like a:hover {
    background-color: #ac5858;
    color: #eeeeee;
  }
  .cart-like i {
    transition: all ease 0.2s;
  }
  .cart-like i:hover {
    transform: scale(0.92);
    transition: all ease 0.2s;
  }
  
  .cart-like a:hover {
    transform: scale(0.92);
    transition: all ease 0.2s;
  }



  /*FOOTER*/
  footer {
    border-top: 1.5px solid #ababab;
    width: 100%;
    min-height: 85vh;
    background-color: #ebebeb;
  }
  
  .logo-footer {
    width: 60%;
    padding-top: 3em !important;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  
  .footer-2-columns {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 3em;
  }
  .footer-2-columns .footer-column {
    float: left;
    margin: 1em;
  }
  .footer-2-columns .footer-column h3 {
    text-transform: uppercase;
    margin-top: 3em !important;
    margin: 0;
    color: #383838;
  }
  .footer-2-columns .footer-column ul {
    list-style: none;
    padding: 0;
  }
  .footer-2-columns .footer-column ul a {
    text-decoration: none;
    color: #7f7f7f;
    line-height: 1.5;
    transition: all ease 0.3s;
  }
  .footer-2-columns .footer-column ul a:hover {
    color: #121212;
    transition: all ease 0.3s;
  }
  
  .newsletter-block {
    text-align: center;
  }
  
  .newsletter-p {
    font-size: 1.2rem;
    text-transform: uppercase;
    text-align: center;
    font-weight: 600;
    color: #383838;
    letter-spacing: 1px;
  }
  
  input {
    border-radius: 0;
    border: 1px solid #7f7f7f;
    width: 214px;
    height: 25px;
  }
  
  .footer-btn {
    cursor: pointer;
    border-radius: 0;
    border: none;
    background-color: #121212;
    color: #ebebeb;
    width: 45px;
    height: 29px;
    transition: all ease 0.3s;
  }
  .footer-btn:hover {
    background-color: #4e4e4e;
    border: 1px solid #121212;
    transition: all ease 0.3s;
  }
  
  .input-btn {
    display: flex;
    justify-content: center;
  }
  
  .social {
    justify-content: center;
    display: flex;
    gap: 1.5em;
    margin-top: 1.5em;
  }
  
  .social-icon {
    width: 1.8em;
    height: auto;
  }